export default {
	data() {
		return {
			cover:''
		}
	},
	methods: {
		longTap(){
			uni.showActionSheet({
				itemList:['保存图片'],
				success: res => {
					if(res.tapIndex ==0){
						uni.showLoading({
							title: '图片保存中...',
						})
						uni.downloadFile({
							url:this.cover,
							success:(result)=>{
								let tempFilePath = result.tempFilePath
								uni.saveImageToPhotosAlbum({
									filePath:tempFilePath,
									success:()=>{
										uni.showToast({
											title:"保存成功",
											duration:2000
										})
									},
									complete:()=>{
										uni.hideLoading()
									}
								})
							}
							
						})

					}
				},
				fail: () => {},
				complete: () => {}
			});
		}
	},
	onLoad(params){
		this.cover = params.cover
		
		uni.setNavigationBarColor({
			frontColor:"#ffffff",
			backgroundColor:"#000000"
		})
	}
}
